.footer {
	position: absolute;
	bottom: 0;
	left: 0;
	
	width: 100%;
	
	text-align: center;
	
	p {
		margin: 3px;
		
		color: #888;
		font: 0.875em sans-serif;
		opacity: 0.75;
		
		.link {
			color: #888;
			text-decoration: none;
			
			&:hover {
				color: #888;
				text-decoration: underline;
			}
		}
	}
}