.app{
	display: grid;
	grid-template-columns: var(--navbar-width) 1fr;
	grid-template-rows: 1fr;
	
	width: 100vw;
	height: 100vh;
	
	background-color: #fff;
	/* background-image: linear-gradient(to bottom, #fff 0%, #fdd 100%); */
}

.app > p {
  position: absolute;
  top: calc(50% - 100px);
  
  width: 100%;
  height: 100%;
  
	color: rgb(175,88,86);
	font: bold 30px sans-serif;
  text-align: center;
  
	opacity: 0.8;
}

