.category{
	display: flex;

	.button{
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		
		width: 100%;
		
		margin: 8px;
		
		font-weight: bold;
		font-size: 11px;
		color: #1f1f21;
		text-decoration: none;
		
		border: 1px solid #cf98a3;
		box-shadow: -1px -1px 3px #e76b81 inset;
		border-radius: 5%;
		
		transition: background var(--transition-speed);
		
		&:hover {
			box-shadow: 1px 1px 4px #be7784 inset;
		}
		
		svg {
			width: calc(var(--navbar-width) * 0.24);
			height: calc(var(--navbar-width) * 0.24);
			
			fill: #f4667f;
			
			transition: width height var(--transition-speed);
		}
	}
	
	.popup{
		position: fixed;
		left: var(--navbar-width);
		bottom: 0;
		
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: stretch;
		
		width: 250px;
		height: 360px;
		
		background-color: #fff;
		
		border: 1px solid #faa4b4;
		border-radius: 2px;
		
		.categoryitem{
			margin: 0 8px;
			padding: 5px;
			
			font-weight: bold;
			font-size: 12px;
			text-decoration: none;
			
			border-radius: 2px;
			
			&.txt--white{
				color: #fff;
			}
			&.txt--eerieblack{
				color: #1f1f21;
			}
			
			&.bg--quicksilver{
				background-color: #a1a1a1;
			}
			&.bg--lavenderfloral{
				background-color: #b38dd9;
			}
			&.bg--newyorkpink{
				background-color: #d98d8d;
			}
			&.bg--tan{
				background-color: #d9b38d;
			}
			&.bg--greenyellowcrayola{
				background-color: #d9d98d;
			}
			&.bg--grannysmithapple{
				background-color: #8dd98d;
			}
			&.bg--middlebluegreen{
				background-color: #8dd9d9;
			}
			&.bg--aero{
				background-color: #8db3d9;
			}
			&.bg--lightgray{
				background-color: #cccccc;
			}
			&.bg--platinum{
				background-color: #e3e3e3; 
			}
		}
	}
}
