*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	overflow: hidden;
  cursor: default;
}

body {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
	--transition-speed: 300ms;
	--navbar-width: 80px;
}
