.sidebar{
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr 1px 50px;
	
	border-right: 1px solid #faa4b4;
	
	z-index: 1;
	
	.navbar{
		display: flex;
		flex-direction: column;
		align-items: center;
		
		.navitem{
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			
			width: calc(var(--navbar-width) * 0.68);
			height: calc(var(--navbar-width) * 0.68);
			margin: 10px 0 0 0;
			
			font-weight: bold;
			font-size: 12px;
			color: #1f1f21;
			text-decoration: none;
			
			border: 1px solid #cf98a3;
			box-shadow: -1px -1px 3px #e76b81 inset;
			border-radius: 5%;
			
			transition: background var(--transition-speed);
			
			&:hover {
				box-shadow: 1px 1px 4px #be7784 inset;
			}
			
			&.active {
				box-shadow: 1px 1px 4px #be7784 inset;
			}
			
			svg {
				width: calc(var(--navbar-width) * 0.32);
				height: calc(var(--navbar-width) * 0.32);
				
				fill: #f4667f;
				
				transition: width height var(--transition-speed);
			}
		}
	}
	
	.line{
		margin: 0 10%;
		border-top: 1px solid #faa4b4;
	}
}